
import { getListTask} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default{
data(){
return{
infoList:[],
detailInfo:{}
}
},
components: {
Group,
Cell,
XDialog,
Icon
},
created(){
this.detailInfo = this.$route.query.info;
let token  = localStorage.getItem("token");
console.log(token);
getListTask(this.detailInfo.businessKey,token).then((result)=>{
this.infoList = result;

})
},
mounted(){
var self = this;
self.mescroll = new MeScroll("taskDetail",{
up:{
isBounce:false,
use:false
},
down:{
use:false
}
})




}

}




