import { getListTask} from '../../service/service.js'
export default{
	data(){
		return{
			infoList:[],
			detailInfo:{},
			showDialog:false,
			dialogInfo:"确定同意该请求吗？"
		}
	},
	methods:{
		agree(){
			this.showDialog = true;
			this.dialogInfo = "确定同意该请求吗？";
		},
		reject(){
			this.dialogInfo = "确定拒绝该请求吗?";
			this.showDialog = true;
		},
		close(){
			this.showDialog = false;
		}

	},
	created(){
		this.detailInfo = this.$route.query.info;
		let token  = localStorage.getItem("token");
		console.log(token);
		getListTask(this.detailInfo.businessKey,token).then((result)=>{
			this.infoList = result;
			
		})
	},
	mounted(){	
		var self = this;
		self.mescroll = new MeScroll("dProcess",{
			up:{
				isBounce:false,
				use:false
			},
			down:{
				use:false
			}
		})	

		
			
	}
	
}
    



